.home_list ul{
    margin:0 auto;
    display: flex;
    flex-wrap: wrap;
    flex:1;
}
.home_list ul li{
    width:25%;
    overflow: hidden;
    position:relative;

}
.home_list ul li> img{
    width: 100%; height: 100%;
}
.home_list ul li a{
    position: relative;
}
.home_list ul li a img{
    position: absolute;
    left: 0; top: 0;
    transform: translate(-50%,0);
}
.home_list ul li a img:nth-last-of-type(1){
    transform: translate(-50%,150%);
}
.home_list ul li b small{
    color: #fff;
}
.home_list ul li b small::after{
    content: '';
    display: block;
    background-color: #fff;
    width: 40px; height: 1.5px;
    margin: 30px auto;
}
.home_list ul li b{
    position: absolute;
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
}
.home_list ul li:hover b{

    color: #fff;
    background:rgba(111, 185, 185, 0.9);
    cursor:url(img/cursor.cur) 4 12, pointer;
    animation: img_hover 0.4s forwards;
}
@keyframes img_hover {
    0%{opacity: 0;}
    100%{opacity: 1;}
}
