*{
    list-style: none;
    text-decoration: none;
    padding:0; margin:0;
}
.hd{
    display: none;
}
.hd_active{
    position:fixed;
    left: 0; top:0;
    width: 220px;    height: 100vh;
    background-color: #14303b;
    box-shadow: 10px 10px 30px;
    display: flex;
    flex-direction:column;
    justify-content: flex-start;
    text-align: right;
    animation: hd_nav 0.5s backwards;
}
@keyframes hd_nav{
    0%{ transform: translate(-100%);}
    100%{ transform: translate(0);}
}
.hd_title{
    width:100%;
    padding:20px;
    background-color:#fff;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.hd_title small{
    color:#888;
}
.hd_nav{
    width:100%
}
.hd_nav >li{
    width:100%;
    padding:20px;
    background: linear-gradient(#365b6d,#14303b);
}
.hd_nav >li >a{
    font-size: 1.2em;
    color:#fff;
}
.hd_nav >li:hover{
    background:#6fb9b9;;
}
.hd_und{
    width: 100%;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    padding-right:20px;
}
.hd_und >p>span{
    font-size: 0.8em;
    color:#6fb9b9;
}
.hd_und >span{
    margin: 5px 0;
    font-size: 0.8em;
    color:#555;
}
.hd_und >span:nth-of-type(1){
    color:#ddd;
}
.hd_icon img{
    width: 18px;
    margin:2px;
}